  background-color: #00000069;
  grid-template-rows: repeat(16, 1fr);
  grid-template-columns: repeat(22, 1fr);
  display: grid;
  position: relative;
  top: 100vh;
}

@media screen and (max-width: 768px) {
  .flip-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
