.flip-container .projects {
  color: #f7ffd6;
  opacity: 0;
  border-top: 2px solid #f7ffd6;
  grid-area: 8 / 11 / 9 / 23;
  font-size: 3.2rem;
  transition: all 1s ease-out;
  transform: translateY(-100%);
}

.flip-container:hover .projects {
  opacity: 1;
  transform: translateY(0%);
}

.flip-container .project1 {
  grid-area: 11 / 11 / 16 / 14;
}

.flip-container .project2 {
  grid-area: 11 / 15 / 16 / 18;
}

.flip-container .project3 {
  grid-area: 11 / 19 / 16 / 22;
}

.flip-container .flip-card {
  perspective: 1000px;
  background-color: #0000;
}

.flip-container .flip-card-inner {
  width: 100%;
  height: 100%;
  text-align: center;
  transform-style: preserve-3d;
  transition: transform .8s;
  position: relative;
}

.flip-container .flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-container .flip-card-front, .flip-container .flip-card-back {
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 6px;
  position: absolute;
}

.flip-container .flip-card-front img, .flip-container .flip-card-back img {
  border-radius: 6px;
}

.flip-container .flip-card-front {
  color: #000;
  background-color: #a8aba4;
}

.flip-container .flip-card-back {
  color: #333131;
  text-align: left;
  background-color: #f7ffd6;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 10px;
  display: flex;
  transform: rotateY(180deg);
  box-shadow: 6px 6px 10px #5c5b5b40, -6px -6px 10px #ffffff4d;
}

.flip-container .flip-card-back a {
  color: #f87427;
  text-decoration: none;
}

.flip-container .flip-card-back a:hover {
  text-decoration: underline #f87427;
  text-decoration-thickness: 2px;
}

